
.news-item {
  padding: 5px 10px 5px 70px;
  border-bottom: 1px solid #ccc;
  position: relative;
  line-height: 20px;
  overflow: hidden;
}
.news-item #info {
  transform: translate3d(0px, 0px, 0px);
  -webkit-transform: translate3d(0px, 0px, 0px);
}
.news-item .button {
  font-size: 12px;
  color: #fff;
  padding: 5px 20px;
}
.news-item .button i {
  font-size: 25px;
}
.news-item .button .text {
  text-align: center;
}
.news-item .title {
  border-bottom: 1px solid #f5f3f3;
  padding: 5px;
}
.news-item .score {
  color: #58b7ff;
  font-size: 1.5em;
  position: absolute;
  top: 50%;
  left: -70px;
  width: 80px;
  text-align: center;
  margin-top: -10px;
}
.news-item .meta,
.news-item .host {
  font-size: 0.85em;
  color: #999;
  padding: 5px;
}
.news-item .meta a,
.news-item .host a {
  color: #999;
  text-decoration: underline;
}
.news-item .meta a:hover,
.news-item .host a:hover {
  color: #f60;
}
.news-item .mint-swipe {
  height: 100%;
}


