
.news-list {
  width: 100%;
  padding-bottom: 50px;
  transition: all .5s cubic-bezier(.55, 0, .1, 1);
}
.news-list .loading {
  text-align:center;
  position: fixed;
  top: 40%;
  left: 40%;
}
.news-list ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
}



